import React from "react";
import "./card2.css";
import { Link } from "react-router-dom";

const Card2 = ({  image, name, link }) => {
  return (
    <div className="card2" >
      <img src={image} alt="" />

      <div className="details">

      <Link to={link}>  
       <button className="cbutton">Live Demo<span>{name}</span></button>
         
      </Link> 
    
      </div>
     
    
    </div>
  );
};

export default Card2; 
