import { Contact, Experience, Intro, Portfolio, Services, Testimonial, Works } from "../../components"
import { useContext } from "react";
import { ThemeContext } from "../../Context";





const Home = () => {


  const theme = useContext(ThemeContext);
  const darkMode = theme.state.darkMode;


  return (
    <div className="gradient__bg" 
    style={
        {background : darkMode ? 'black' : '',
        color : darkMode ? "#fff" : ''
    }
    }
    >
    <Intro />
    <Services />
    <Experience />
    <Works />
    <Portfolio />
    <Testimonial />
    <Contact />

    </div>
  )
}

export default Home