import Card from '../card/Card';
import './Services.css'
import Resume from './resume.pdf'
import { motion } from 'framer-motion';
import { useContext } from 'react';
import { ThemeContext } from "../../Context";






const Services = () => {

    // context
  const theme = useContext(ThemeContext);
  const darkMode = theme.state.darkMode;

  // transition
  const transition = {
    duration: 1,
    type: "spring",
  };


    return ( 
        <div 
            className="services" 
            id='services' 
            style={ 
                    darkMode 
                    ? {color : "var(--gradient-bg)", background : "white"} 
                    : {color : "white", background : "var(--gradient-bg)"}}
                    >
                <div className="top_header" >
                    <h1 className="header" style={{ color: !darkMode ? "white" :" #500f12" }}>My Awesome</h1>
                        <h3 style={{ color: !darkMode ? "white" : "black" }}>services</h3>
                            <p className="intro">As a passionate website designer, 
                            branding specialist, and digital marketing expert, I offer a 
                            range of services to help businesses and individuals establish a strong online presence,
                            create a captivating brand identity, and drive growth in the digital landscape.
                            </p>

                                {/* left side */}
                </div>


                <div className='info' style={{ color: !darkMode ? "white" : "#555" }}>
                    <div className="info_card">
                        <p className="webdesign"><span className='title'>Website Design:</span>
                            A well-designed website is crucial in today's digital world. 
                            I create stunning and user-friendly websites tailored to your unique needs. 
                            From eye-catching visuals to seamless navigation, my websites ensure an immersive
                             user experience that leaves a lasting impression.
                            </p>
                            <div className='hr'></div>
                        <p className="webdesign"><span className='title'>Branding Services:</span>
                            Your brand is more than just a logo; it's a representation of your values 
                            and personality. Let me craft a compelling brand identity that tells your story 
                            and resonates with your target audience. From logos to color palettes and brand 
                            guidelines, I'll help you stand out in the competitive market.
                            </p>
                       
                    </div>




           
                    <div className='info_card'>
                    <p className="webdesign"><span className='title'>E-commerce Solutions:</span>
                        For businesses looking to sell products online, I offer e-commerce solutions
                         that streamline your online store and enhance the shopping experience. 
                         From product listings to secure payment gateways, I'll create a seamless
                          platform to boost your sales.
                          </p>
                          <div className='hr'></div>


                    <p className="webdesign"><span className='title'>Responsive Design:</span>
                        In today's mobile-first world, responsive design is paramount. 
                        I ensure that your website looks stunning and functions flawlessly across all devices, 
                        from desktops to smartphones, providing a consistent user experience.
                        </p>
                        <div className='hr'></div>

                        <p className="webdesign"><span className='title'>Digital Marketing:</span>
                            Reach and engage your audience effectively with my digital marketing strategies. 
                            Through SEO, social media marketing, content creation, and email campaigns,
                             I'll drive targeted traffic to your website and boost your online visibility. 
                             Together, we'll convert leads into loyal customers.
                            </p>

                    
                    <div>
                        <a href={Resume} download>

                            <button className="button ">
                                Download CV
                            </button>
                        </a>
                    </div>

                    </div>
            
                                 {/* right side */}


                    <div className="info_card">
                    <p className="webdesign"><span className='title'>Consultation and Support:</span>
                        I believe in open communication and collaboration. I'll work closely with you throughout the process, 
                        offering expert advice and support to ensure your vision becomes a reality.
                    </p>
                    <div className='hr'></div>

                    <p className="webdesign"><span className='title'>Analytics and Optimization:</span>
                        Data-driven decisions lead to success. I track website performance, analyze user behavior,
                         and make data-backed improvements to enhance your website's effectiveness and conversion rates.</p>

                         <div className='hr'></div>


                    <p className="webdesign">Let's take your digital presence to the next level. 
                    Contact me to discuss your project, and together, we'll create a remarkable
                     online journey that sets you apart from the rest.
                     </p>
                     

                    

                    </div>

                </div>
        </div>
     );
}
 
export default Services;