import { RiMenu3Line, RiCloseLine } from 'react-icons/ri';
import logo from '../../assets/logo.png';
import './navbar.css';
import { Link } from 'react-router-dom';
import Toggle from '../toggle/Toggle';

import { useState } from "react";
import { useContext } from "react";
import { ThemeContext } from "../../Context";




const Menu = () => {
    return (
        <ul className='nav_list'>
          <li>
            <Link to="/">Home</Link>
          </li>
          <li>
            <Link to="services">Services</Link>
          </li>
          <li>
            <Link to="experience">Experience</Link>
          </li>
          <li>
            <Link to="portfolio" >Portfolio</Link>
          </li>
          <li>
            <Link to="testimonial">Testimonial</Link>
          </li>
          <li>
            <Link to="contact">Contact</Link>
          </li>
        </ul>
    )
}


const Navbar = () => {

  const theme = useContext(ThemeContext);
  const darkMode = theme.state.darkMode;
  
  const [toggleMenu, setToggleMenu] = useState(false);

  return (
    <div className="BnB__navbar" style={ darkMode ? {color : "black", background : "white"} : {color : "white", background : "var(--gradient-inverse-bg)"} }>
      
     

      <div className='left_side'>

      <div className="BnB__navbar-links_logo">
          <img src={logo} alt='logo'/>
      </div>

      <div className="n-name">Engr Chukwuemeka</div>
      <Toggle />
      </div>

      
      <div className="right_side BnB__navbar-links">
        
        <div className="BnB__navbar-links_container">
        <Menu />
        </div>

        <div className="BnB__navbar-menu" style={ darkMode ? {color : "black", background : ""} : {color : "white", background : ""} }>
        {toggleMenu
          ? <RiCloseLine color="#fff" size={27} onClick={() => setToggleMenu(false)} style={ darkMode ? {color : "black", background : ""} : {color : "white", background : ""} }/>
          : <RiMenu3Line color="#fff" size={27} onClick={() => setToggleMenu(true)} style={ darkMode ? {color : "black", background : ""} : {color : "white", background : ""} }/>}
        {toggleMenu && (
        <div className="BnB__navbar-menu_container" 
            style={ 
                darkMode 
                ? {color : "#FF4820", background : "#f5f5f5" , border: "2px solid #FF4820"} 
                  : {color : "white", background : "#040C18", border: "2px solid white"} 
                  }>
          
              <ul className='mobileNav_list' onClick={() => setToggleMenu(false)}>
                  <li>
                    <Link to="/">Home</Link>
                  </li>
                  <li>
                    <Link to="services">Services</Link>
                  </li>
                  <li>
                    <Link to="experience">Experience</Link>
                  </li>
                  <li>
                    <Link to="portfolio" >Portfolio</Link>
                  </li>
                  <li>
                    <Link to="testimonial">Testimonial</Link>
                  </li>
                  <li>
                    <Link to="contact">Contact</Link>
                  </li>
              </ul>
          </div>
       
        )}
      </div>

      </div>

      
     

  
     
    </div>
  );
};

export default Navbar;
