import './Portfolio.css'
// import { Swiper, SwiperSlide} from 'swiper/react';
// import SwiperCore, { Pagination, Autoplay} from 'swiper';
import 'swiper/css/pagination'
import 'swiper/css/autoplay'
import 'swiper/css'
import Portsite from '../../assets/img/portfolioPics/portfolio01.png'
import AIWebsite from '../../assets/img/portfolioPics/portfolio02.png'
import dataWebsite from '../../assets/img/portfolioPics/portfolio03.png'
import gymWebsite from '../../assets/img/portfolioPics/portfolio04.png'
import PORDMinistries from '../../assets/img/portfolioPics/portfolio05.png'
import ContainerSite from '../../assets/img/portfolioPics/portfolio06.png'
import Videoapp from '../../assets/img/portfolioPics/portfolio07.png'
import Restaurant from '../../assets/img/portfolioPics/portfolio08.png'
import { Link } from 'react-router-dom';
import Card2 from '../card2/Card2';
import { useContext } from 'react'
import { ThemeContext } from '../../Context'



const Portfolio = () => {

    const theme = useContext(ThemeContext);
    const darkMode = theme.state.darkMode;



const Profiles = [
    {
        id: 1,
        image: gymWebsite,
        name: 'BnB Gym',
        link: "https://bchukwuemeka.github.io/brichandbezgym/"
    },
    {
        id: 2,
        image: PORDMinistries,
        name: 'Church Website',
        link: "https://bchukwuemeka.github.io/pordministries/"
    },
    {
        id: 3,
        image: AIWebsite,
        name: 'Bnb Innovation',
        link: "https://bchukwuemeka.github.io/brichandbezai/"
    },
    {
        id: 4,
        image: Portsite,
        name: 'BnB Portfolio',
        link: 'https://BChukwuemeka.github.io/portfolio'
    },
    {
        id: 5,
        image: Videoapp,
        name: 'BnB Video App',
        link: "https://bchukwuemeka.github.io/bnbvideoapp/"
    },
    {
        id: 6,
        image: Restaurant,
        name: 'BnB Restaurant App',
        link: "https://bchukwuemeka.github.io/bnbrestaurant/"
    },
    {
        id: 7,
        image: dataWebsite,
        name: 'DataWebsite',
        link: 'https://BChukwuemeka.github.io/brichandbezwebsite'
    },
    {
        id: 8,
        image: ContainerSite,
        name: 'BnB Containers',
        link: "https://bchukwuemeka.github.io/portsmouthservices/"
    }
]



//  SwiperCore.use([Autoplay])

    return ( 
            <div 
                className="portfolio" 
                id='portfolio' 
                style={ 
                        darkMode 
                        ? {color : "var(--gradient-bg)", background : "white"} 
                        : {color : "white", background : "var(--gradient-bg)"}} >
                    
                    <div className="heading">
                        <span>Recent Projects</span>
                        <span>Portfolio</span>
                        <span>Touch Image to go to Website</span>
                    </div>
          

                    

  
          <section className="PortfolioImgs">
            <div className="container PortfolioImgs__container">
                    {
                        Profiles.map(({ id, image, name, link , description}) => {
                            return (
                                <Link 
                                    style={{textDecoration: 'none'}} 
                                    to={link} >
                                        <Card2 
                                            key={id} 
                                            image={image} 
                                            name={name} 
                                            description={description} 
                                            link={link}
                                        />
                                        </Link>
                                )}
                            )}
                                
                    </div>
                    </section>
        </div>
     );
}
 
export default Portfolio;